<template>
  <div class="mini-mnist">
    <canvas
      id="mnist-draw"
      ref="mnistDraw"
      width="300"
      height="300"
      @mousedown="handleDrawMouseDown"
      @mousemove="handleDrawMouseMove"
      @mouseup="handleDrawMouseUp"
    />
    <canvas
      id="mnist-hidden"
      ref="mnistHidden"
      width="28"
      height="28"
    />
    <div>
      <button @click="handleClear">Clear</button>
    </div>
    <div>
      {{ answer }}
    </div>
  </div>
</template>

<script>
import * as tf from '@tensorflow/tfjs';

export default {
  name: 'MiniMnist',
  data() {
    return {
      model: null,
      drawing: false,
      lastMousePos: {
        x: 0,
        y: 0,
      },
      answer: null,
    };
  },

  methods: {
    handleDrawMouseDown(event) {
      this.drawing = true;
      this.lastMousePos.x = event.offsetX;
      this.lastMousePos.y = event.offsetY;

      const ctx = this.$refs.mnistDraw.getContext("2d");
      ctx.lineWidth = 15;
      ctx.lineCap="round";
      ctx.lineJoin = "round";
      ctx.beginPath();
    },

    handleDrawMouseMove(event) {
      if (!this.drawing) {
        return;
      }

      const ctx = this.$refs.mnistDraw.getContext("2d");
      ctx.moveTo(this.lastMousePos.x, this.lastMousePos.y);
      ctx.lineTo(event.offsetX, event.offsetY);
      ctx.stroke();

      this.lastMousePos.x = event.offsetX;
      this.lastMousePos.y = event.offsetY;
    },

    handleDrawMouseUp() {
      this.drawing = false;

      const hiddenCtx = this.$refs.mnistHidden.getContext("2d");
      hiddenCtx.clearRect(0, 0, 28, 28);
      hiddenCtx.drawImage(this.$refs.mnistDraw, 0, 0, 28, 28);

      this.makePrediction();
    },

    handleClear() {
      const ctx = this.$refs.mnistDraw.getContext("2d");
      ctx.clearRect(0, 0, 300, 300);
      this.answer = null;
    },

    async makePrediction() {
      const imgData = tf.div(tf.fromPixels(this.$refs.mnistHidden, 4).asType('float32'), 255.);
      const alphaChannel = imgData.slice([0, 0, 3], [28, 28, 1]).expandDims();
      const prediction = this.model.predict(alphaChannel);
      const answer = tf.argMax(prediction.squeeze());
      const answerData = await answer.data();
      [this.answer] = answerData;
    },
  },

  async created() {
    this.model = await tf.loadModel('models/mnist/model.json');
  }
}
</script>

<style>
#mnist-draw {
  border: 1px solid black;
}

#mnist-hidden {
  display: none;
}
</style>
